body {
	font-family: "Poppins", sans-serif;
	font-size: 16px;
}

.headings {
	font-family: "Oswald", sans-serif;
	text-transform: uppercase;
	font-size: 35px;
	line-height: 50px;
	color: $headings;
	display: block;
}

.headings--white {
	color: $white;
	margin: 20px 0;
}

.logos__item {
	font-family: "Oswald", sans-serif;
	text-transform: uppercase;
	font-style: italic;
	color: #f9e8dc;
	font-size: 15px;
}

.nav {
	font-family: "Poppins", sans-serif;
	text-transform: uppercase;
	font-weight: 600;
	font-size: 14px;
}

.actions {
	font-size: 20px;
	text-transform: uppercase;
	font-family: "Oswald", sans-serif;
	font-weight: 500;
}

.headings--etr-boost {
	text-align: center;

	&::after {
		content: "";
		display: block;
		margin-top: 25px;
		margin-bottom: 30px;
		background: url("../images/line-figure.png") 50% 0% no-repeat;
		height: 18px;
		max-width: 963px;
	}
}

.text {
	text-align: center;
	line-height: 24px;
	color: $grey;
}

.text--services {
	text-align: left;
}