.contacts {
	background-color: #0b091a;
	background-image: url("../images/contacts-top-bg.jpg");
	background-position: center top;
	background-repeat: no-repeat;
	width: 100%;
}

.container--contacts {
	padding-top: 100px;
	padding-bottom: 30px;
}

.socials {


	&__item {
		display: inline-block;
	}

	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 40px;
		height: 46px;
		background: url("../images/socials-bg.png");
		
		&:hover {
			background-position: 50% 100%;
		}

		&:hover .socials-svg {
			fill: #fff;
		}
	}
}




.socials-svg {
	fill: #ef4b2f;
	transition: fill .3s;
}
