.triggers {
	background: url("../images/tabs-bg.jpg") center top no-repeat;
	padding-top: 230px;
	padding-bottom: 230px;
}

.tabs {
	width: 950px;
	min-height: 430px;
	border-radius: 10px;
	background-image: url("../images/tabs-bg.png"), url("../images/overwatch-logo.png"), linear-gradient( 40deg, rgb(248,72,113) 0%, rgb(250,179,98) 100%);
	background-position: 0% 0%, 95% 85%;
	background-repeat: no-repeat;
	background-size: contain, auto;
	margin: auto;
	padding: 80px 110px 80px 390px;
	color: #fff;
	font-size: 16px;
	line-height: 24px;
	position: relative;

	&::before {
		content: url("../images/triggers-img.png");
		position: absolute;
		bottom: -84px;
		left: -310px;
	}

	&__list {
		display: flex;
	}

	&__item {
	display: flex;
	justify-content: center;
	align-items: center;
	transition: opacity .3s;
	width: 71px;
	height: 82px;
	margin: 7px;
	cursor: pointer;

		&.active {
			background: url("../images/tabs-item-bg.png") no-repeat;
		}
		&.active .cls-1 {
			opacity: 1;
		}
		svg {
			display: block;
			margin: auto;
		}
		&:hover {
			cursor: pointer;
		}
		&:hover .cls-1 {
			opacity: 1;
		}
	}
}


.tabs-content {
	max-width: 440px;
}

.cls-1 {
	fill: #fff;
	fill-rule: evenodd;
	opacity: 0.7;
	margin: auto;
}

.tabs-content__item {
	display: none;

	&.active {
		display: block;
	}
}

