.container--services {
	display: flex;
	justify-content: space-between;
	padding-top: 75px;
	padding-bottom: 100px;
}

.services-img {
	display: block;
	margin-left: 60px;
	margin-right: 50px;
	align-self: center;
}

.services-info {
	width: 60%;
}


.headings--services {
	margin-top: 50px;
	margin-bottom: 40px;
}

.text--services {
	height: 185px;
	overflow-y: scroll;
}

.contact-us {
	display: inline-block;
	border-radius: 5px;
	background-image: $bluegradient;
	margin: 40px 0;
	padding: 15px 40px;
	text-transform: uppercase;
	color: $white;
	font-size: 14px;
	font-weight: 500;
}