.etr-boost {
  padding-bottom: 40px;
  background-image: url("../images/etr-bg-left.png"), url("../images/etr-bg-right.png");
  background-position: 3% 60%, 95% 60%;
  background-repeat: no-repeat;
}

.cards {
  padding-top: 40px;
  padding-bottom: 20px;
  display: flex;
  justify-content: space-between;
}

.card__photo {
  // border: 2px solid $peach;
  // border-radius: 50%;
}

.card {
  width: 270px;
  box-shadow: 0px 13px 36px 4px rgba(25, 28, 65, 0.23);
  border-radius: 5px;
  background-color: $white;

  &__info {
    background-color: $darkblue;
    color: $white;
    width: 100%;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
    padding: 2px 2px 0 2px;
  }

  &__player {
    text-transform: uppercase;
    font-size: 12px;
    margin-left: -15px;
  }

  &__name {
    display: block;
    font-weight: 600;
    text-transform: none;
    font-size: 16px;
  }

  &__time {
    text-align: center;
    font-size: 12px;  
    line-height: 18px;
  }
  
 &__container {
  border-color: #e6e6e6;
  border-width: 1px;
  border-style: solid;

    &--info {
      border-top-right-radius: 5px;
      border-top-left-radius: 5px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 20px 23px;
    }

    &--level {
      padding: 10px;
      border-bottom: none;
    }

    &--progress {
      border-top: none;
      border-bottom-right-radius: 5px;
      border-bottom-left-radius: 5px;
    }
 }

}

.level {
  padding: 0 2px;
}

.levels-holder {
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 25px;

  &__arrows {
    align-self: center;
  }
}

.levels {
  width: 100px;
  height: 140px;
  padding: 5px;

  &__condition {
    text-transform: uppercase;
    display: block;
    text-align: center;
    padding-bottom: 8px;
    font-size: 12px;
    font-weight: 600;
  }

  &__img {
    display: block;
    margin: auto;
    align-self: center;
  }

  &__metal {
    display: block;
    font-weight: 600;
    text-align: center;
    padding-top: 5px;
    font-size: 14px;
  }

  &--center {
    margin: auto;
  }
}

.progress {
  color: #102c40;
  text-transform: uppercase;
  text-align: center;
  padding: 0 2px 2px 2px;

  &__text {
    display: block;
    font-style: normal;
    font-size: 12px;
    padding: 10px;
  }

  &__percent {
    font-weight: 600;
    font-size: 20px;
  }
}

.progress-bar {
  width: 245px;
  height: 6px;
  border-radius: 3px;
  background-color: #e8eaf1;
  margin: 10px auto;

  &__value {
    background-color: #ef381a;
    border-radius: 3px;
    height: 6px;
    transform: scaleX(.65);
    transform-origin: 0 0;

    &--85 {
      transform: scaleX(.85);
    }
    &--30 {
      transform: scaleX(.3);
    }
    &--50 {
      transform: scaleX(.5);
    }
  }
}
