.header {
	background: #ccc url("../images/header-bg.jpg") no-repeat;
	height: 753px;
	color: $white;
}

.menu {
	background-color: rgba(#0c143c, .7);
	padding-top: 20px;
	padding-bottom: 28px;
}

.container{
	&--menu {
		display: flex;
		justify-content: space-between;
	}
	&--actions {
		position: relative;
	}
}

.logos {
	&__item {
		display: inline-block;
		position: relative;
		padding-left: 50px;
		padding-right: 30px;
	}
	&__link {
		span {
			color: $red;
			font-size: 13px;
		}
		&--overwatch-boosting::before {
			content: url("../images/overwatch-boosting.png");
			position: absolute;
			left: -5px;
			top: -10px;
		}
		&--eloboost-league::before {
			content: url("../images/eloboost.png");
			position: absolute;
			left: -5px;
			top: -10px;
		}
	}
}

.nav {
	&__item {
		display: inline-block;
	}

	&__link {
		display: block;
		padding: 14px 18px;
		transition: color .3s ease-in-out;

		&:hover {
			color: #6b76d5;
		}

		&--login {
			border-radius: 5px;
			background-image: $bluegradient;
			margin-left: 30px;
			margin-right: 10px;
			padding-left: 40px;
			padding-right: 40px;
			transition: box-shadow .3s;

			&:hover {
  				box-shadow: 7px 12px 37px 4px rgba(0, 0, 0, 0.46);
  				color: #fff;
			}
		}
	}
}

.actions {
	padding-top: 100px;

	&__list {

	}
	&__item {
		position: relative;
		margin-top: 15px;
		margin-bottom: 61px;

		&--duo-boosting {
			padding-left: 43px;
		}

		&--demonstration {
			padding-left: 22px;
		}

		&::before {
			content: "";
			display: block;
			width: 40px;
			height: 60px;
			background-color: $peach;
			transform: skewX(170deg);
			border-radius: 5px;
			position: absolute;
		}
	}
	&__link {
		display: block;
		padding-top: 20px;
		padding-left: 25px;
		padding-bottom: 20px;
		position: relative;
		z-index: 5;
	}
}

.header-img {
	position: absolute;
	top: 23px;
	right: -40px;
}